* {
    margin: 0;
    padding: 0;
}

body {
    background-color: rgba(0, 0, 0, .4);
}

.large {
    height: 80vh;
}

.img {
    max-height: 40em;
}

.img-container {
    position: relative;
}

.up {
    position: absolute;
    font-size: 2rem;
    text-align: center;
    left: 52%;
    transform: translateX(-50%);
    margin: .3em 0;
    font-family: impact,
        sans-serif;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 2px;
    text-shadow: 2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.down{
    position: absolute;
    font-size: 2rem;
    bottom: .5em;
    text-align: center;
    left: 52%;
    transform: translateX(-50%);
    margin: .3em 0;
    font-family: impact,
    sans-serif;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 2px;
    text-shadow: 2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}